import React, { Component } from "react";
import { Form, Input, Button, Row, Col, Alert } from "antd";
import FloatLabel from "../form/FloatLabels";
import { CommonHeader } from "../../containers";
import Amccvector from "../../assets/images/amcc-vector.svg";
import "./commonLogin.scss";
import { RESET_PASSWORD, SET_NEW_PASSWORD } from "../../headings";
import { LOGIN_PART1, LOGIN_PART2 } from "../../messages";
import {
  ENTER_NEW_PASSWORD,
  PASSWORD_RULES,
  CONFIRM_YOUR_PASSWORD,
  PASSWORD_NOT_MATCH,
} from "../../errors";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { CONFIRM_PASSWORD, PASSWORD } from "../../labels";

const layout = {
  wrapperCol: {
    span: 24,
  },
};
const tailLayout = {
  wrapperCol: {
    span: 24,
  },
};

class ResetPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      password: "",
      confirm_password: "",
      error: "false",
      passwordValidationError: "",
      resetDone: false,
    };
  }

  onChangePassword = (e) => {
    this.setState({
      password: e.target.value,
    });
  };

  onChangeConfirmPassword = (e) => {
    this.setState({
      confirm_password: e.target.value,
    });
  };

  resetPassword = (e) => {
    console.log('propssss: ', this.props);
    const { token, uidb64 } = this.props.location.search;
    const data = {
      password: this.state.password,
      token: token,
      uidb64: uidb64,
    };
    const options = {
      headers: {
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    if (
      this.state.password === this.state.confirm_password &&
      !this.state.passwordValidationError
    ) {
      axios
        .put(BASE_URL + "password-reset-complete/", data, options)
        .then(() => {
          this.setState({ resetDone: true });
        })
        .catch((error) => {
          if (error.message.includes("400")) {
          }
        });
    }
  };

  validatePassword = (e) => {
    var re = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,}$/;
    if (!re.test(e.target.value)) {
      this.setState({ passwordValidationError: true });
    } else {
      this.setState({ passwordValidationError: false });
    }
  };

  render() {
    return (
      <>
        <Row className="px-4 py-1 py-lg-3 header">
          <CommonHeader />
        </Row>
        <Row className="content-center mt-0 px-4">
          <Col
            sm={24}
            lg={12}
            className="vector-area justify-content-center mt-1"
          >
            <Col span={24} className="text-center">
              <h5 className="text-center primary-text">{LOGIN_PART1}</h5>
            </Col>
            <Row className="justify-content-center mt-3">
              <img src={Amccvector} alt="Anonymoose" className="pr-0 pr-lg-4" />
            </Row>
            <Row className="pt-3">
              <p className="w-100 text-center text-wgt-md mb-1 d-none d-lg-block">
                {LOGIN_PART2}
              </p>
            </Row>
          </Col>
          <Col
            lg={{ span: 12 }}
            md={{ span: 24 }}
            xs={{ span: 24 }}
            className="d-flex justify-content-center mt-2"
          >
            <div className=" col-md-24 col-lg-12 col-xl-10 login-box justify-content-center d-flex">
              <Row className="mb-4 d-none d-lg-block">
                <h2>{RESET_PASSWORD}</h2>
                <p className="mt-3 font-weight-bold">{SET_NEW_PASSWORD}</p>
              </Row>
              {this.state.resetDone ? "PASSWORD HAS BEEN RESET" : null}
              <Form>
                <FloatLabel label={PASSWORD} value={this.state.password}>
                  <Form.Item
                    name="password"
                    rules={[
                      {
                        required: true,
                        message: ENTER_NEW_PASSWORD,
                      },
                    ]}
                  >
                    <Input.Password
                      htmlFor="password"
                      id="password"
                      className="pwd required"
                      value={this.state.password}
                      onChange={(e) => this.onChangePassword(e)}
                      onBlur={(e) => this.validatePassword(e)}
                    />
                    {this.state.passwordValidationError && (
                      <Alert
                        message={PASSWORD_RULES}
                        type="error"
                        showIcon
                      ></Alert>
                    )}
                  </Form.Item>
                </FloatLabel>

                <FloatLabel
                  label={CONFIRM_PASSWORD}
                  value={this.state.confirm_password}
                >
                  <Form.Item
                    name="confirmpassword"
                    rules={[
                      {
                        required: true,
                        message: CONFIRM_YOUR_PASSWORD,
                      },
                      ({ getFieldValue }) => ({
                        validator(_, value) {
                          if (!value || getFieldValue("password") === value) {
                            return Promise.resolve();
                          }

                          return Promise.reject(PASSWORD_NOT_MATCH);
                        },
                      }),
                    ]}
                    dependencies={["password"]}
                    hasFeedback
                  >
                    <Input.Password
                      htmlFor="confirmpassword"
                      className="pwd confirpwd required"
                      id="confirmpassword"
                      className="input-control"
                      value={this.state.confirm_password}
                      onChange={(e) => this.onChangeConfirmPassword(e)}
                    />
                  </Form.Item>
                </FloatLabel>

                <Form.Item {...tailLayout}>
                  <Button
                    type="primary"
                    htmlType="submit"
                    className="primary-btn-lg w-100 mt-4"
                    onClick={(e) => this.resetPassword(e)}
                  >
                    {RESET_PASSWORD}
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </Col>
        </Row>
      </>
    );
  }
}

export default ResetPassword;
